import { Shift } from '../types';

export const nurseShifts: Shift[] = [
  { id: 'morning', code: '', name: '', color: '#000000' },
  { id: 'morning_special', code: '*', name: '*', color: '#000000', backgroundColor: '#e3f2fd' },
  { id: 'afternoon', code: '', name: '', color: '#000000' },
  { id: 'night', code: '', name: '', color: '#000000' },
  { id: 'morning_afternoon', code: '', name: '', color: '#000000' },
  { id: 'night_afternoon', code: '', name: '', color: '#000000' },
  { id: 'training', code: '', name: '', color: '#000000' },
  { id: 'night_training', code: '', name: '', color: '#000000' },
  { id: 'off', code: 'O', name: 'Off', color: '#ffffff', backgroundColor: '#f44336' },
  { id: 'vacation', code: 'va', name: 'Vacation', color: '#ffffff', backgroundColor: '#d32f2f' },
  { id: 'other', code: '', name: '', color: '#000000', backgroundColor: '#ffeb3b' },
];

export const assistantShifts: Shift[] = [
  { id: 'morning', code: '', name: '', color: '#000000' },
  { id: 'afternoon', code: '', name: '', color: '#000000' },
  { id: 'night', code: '', name: '', color: '#000000' },
  { id: 'morning_afternoon', code: '', name: '', color: '#000000' },
  { id: 'night_afternoon', code: '', name: '', color: '#000000' },
  { id: 'housekeeping', code: 'MB', name: '', color: '#000000' },
  { id: 'housekeeping_afternoon', code: 'MB', name: '', color: '#000000' },
  { id: 'training', code: '', name: '', color: '#000000' },
  { id: 'night_training', code: '', name: '', color: '#000000' },
  { id: 'off', code: 'O', name: 'Off', color: '#ffffff', backgroundColor: '#f44336' },
  { id: 'vacation', code: 'va', name: 'Vacation', color: '#ffffff', backgroundColor: '#d32f2f' },
  { id: 'other', code: '', name: '', color: '#000000', backgroundColor: '#ffeb3b' },
];

export const allShifts = [...nurseShifts, ...assistantShifts];

export const getShiftByCode = (code: string): Shift | undefined => {
  return allShifts.find(shift => shift.code === code);
};

export const getShiftsByType = (type: 'nurse' | 'assistant'): Shift[] => {
  return type === 'nurse' ? nurseShifts : assistantShifts;
}; 