import { format, getDaysInMonth, startOfMonth, endOfMonth, eachDayOfInterval, isWeekend, getDay } from 'date-fns';
import { th } from 'date-fns/locale';
import { Holiday } from '../types';

//  ()
export const thaiHolidays: Holiday[] = [
  { date: '2024-01-01', name: '', type: 'public' },
  { date: '2024-01-16', name: '', type: 'public' },
  { date: '2024-02-10', name: '', type: 'public' },
  { date: '2024-04-06', name: '', type: 'public' },
  { date: '2024-04-13', name: '', type: 'public' },
  { date: '2024-04-14', name: '', type: 'public' },
  { date: '2024-04-15', name: '', type: 'public' },
  { date: '2024-05-01', name: '', type: 'public' },
  { date: '2024-05-04', name: '', type: 'public' },
  { date: '2024-05-22', name: '', type: 'public' },
  { date: '2024-06-03', name: '', type: 'public' },
  { date: '2024-07-28', name: ' 10', type: 'public' },
  { date: '2024-08-12', name: '', type: 'public' },
  { date: '2024-10-23', name: '', type: 'public' },
  { date: '2024-12-05', name: '', type: 'public' },
  { date: '2024-12-10', name: '', type: 'public' },
  { date: '2024-12-31', name: '', type: 'public' },
];

export const getMonthDays = (year: number, month: number) => {
  const start = startOfMonth(new Date(year, month - 1));
  const end = endOfMonth(new Date(year, month - 1));
  
  return eachDayOfInterval({ start, end });
};

export const isHoliday = (date: Date): Holiday | null => {
  const dateStr = format(date, 'yyyy-MM-dd');
  
  // 
  if (isWeekend(date)) {
    return {
      date: dateStr,
      name: getDay(date) === 0 ? '' : '',
      type: 'weekend'
    };
  }
  
  // 
  const holiday = thaiHolidays.find(h => h.date === dateStr);
  return holiday || null;
};

export const getWorkingDays = (year: number, month: number) => {
  const days = getMonthDays(year, month);
  return days.filter(day => !isHoliday(day));
};

export const getHolidaysInMonth = (year: number, month: number) => {
  const days = getMonthDays(year, month);
  const holidays: Holiday[] = [];
  
  days.forEach(day => {
    const holiday = isHoliday(day);
    if (holiday) {
      holidays.push(holiday);
    }
  });
  
  return holidays;
};

export const formatThaiDate = (date: Date) => {
  return format(date, 'd MMMM yyyy', { locale: th });
};

export const formatThaiMonth = (date: Date) => {
  return format(date, 'MMMM yyyy', { locale: th });
};

export const getDayName = (date: Date) => {
  const dayNames = ['', '', '', '', '', '', ''];
  return dayNames[getDay(date)];
};

